body{
  background: #151515;
}

.spin{
  animation: spin 1s;
}

.back{
  position: fixed;
  width: 100%;
  height: 100%;
}

.back img{
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.transparent{
  content: '';
  width: 100%;
  height: 100%;
  position: fixed;
  background: linear-gradient(0deg, rgb(0,0,0) 0%, rgba(15,23,30,0.35057773109243695) 100%);
  z-index: 100;
}

.leftBar{
  position: fixed;
  width: 400px;
  height: 40vh;
  background: transparent;
  top: 50%;
  left: 3vw;
}

.link{
  color: #bbb7b7;
  list-style: none;
  padding: 10px;
  font-size: 19px;
  cursor: pointer;
}

.link:hover{
  color: #fff;
}

*{
  user-select: none;
}

@keyframes spin{
  0%{
    transform: rotate(0deg)
  }
  100%{
    transform: rotate(360deg)
  }
}